import React, { Component, PropTypes } from "react";

import { color } from "../../vars";

import Hero from "../../components/Hero";
import Container from "../../components/Container";
import Column from "../../components/Column";
import BlockGrid from "../../components/BlockGrid";

class Work extends Component {
  componentWillMount() {
    this.state = {
      theme: {
        primary: color.sky,
        secondary: color.blue,
      },
    };
    document.body.style.backgroundColor = this.state.theme.primary;
    document.body.style.color = this.state.theme.secondary;
  }

  render() {
    return (
      <div>
        <Hero headline="Selected Work." />
        <Container>
          <Column width="full">
            <BlockGrid
              title="UX Design"
              client=""
              url="/work/ux"
              path="ux"
            />
            <BlockGrid
              title="UI Design"
              client=""
              url="/work/ui"
              path="ui"
            />
            <BlockGrid
                title="Brand Design"
                client=""
                url="/work/brand"
                path="brand"
            />
            <BlockGrid
              title="Photography"
              client=""
              url="/work/photography"
              path="photography"
            />
            <BlockGrid
              title="Painting"
              client=""
              url="/work/painting"
              path="painting"
            />
            <BlockGrid
              title="Graphic Design"
              client=""
              url="/work/graphic"
              path="graphic"
            />
          </Column>
        </Container>
      </div>
    );
  }
}

export default Work;



