import React, { Component, PropTypes } from "react";

import { color } from "../../vars";

import Hero from "../../components/Hero";
import Container from "../../components/Container";
import Column from "../../components/Column";
import BlockFeature from "../../components/BlockFeature";
import ListItem from "../../components/ListItem";
import AnchorLink from "../../components/AnchorLink";
import ClientLogo from "../../components/ClientLogo";
import Media from "../../components/Media";
import Button from "../../components/Button";
import IntroContent from "../Publications/intro.md";

class Home extends Component {
  componentWillMount() {
    this.state = {
      theme: {
        primary: color.xr,
        secondary: color.white,
      },
    };
    document.body.style.backgroundColor = this.state.theme.primary;
    document.body.style.color = this.state.theme.secondary;
  }

  render() {
    return (
      <div>
        <Hero
          headline="Machine Learning, Information Art, Human-Computer Interaction, and Design."
        />
        <hr />
      <Container>
          <Column width="full" title="News">
              <ListItem
                  title="IUI 2022"
                  summary="My first-author paper titled 'Interpretable Aesthetic Analysis Model for Intelligent Photography Guidance Systems' has been accepted to IUI 2022 (27th Annual Conference on Intelligent User Interfaces)"
                  link=""
              />
          </Column>
      </Container>
          <hr />
        <Container>
            <Column width="twoTenth" title="Research Overview">
            </Column>
          <Column width="sixTenth">
              <Media
                  media='image'
                  url='../images/overview.png'
                  type='media'
                  gradient='false'
                  color={this.state.theme.primary} />
          </Column>
            <Column width="twoTenth">
            </Column>
        </Container>
        <hr />
        <Container>
          <Column width="full" title="Selected Work">
          </Column>
            <Container>
                <Column
                    width='full'
                    intro={<AnchorLink type="hyper" link="https://xiaoranwu18.github.io/playground/" text="Deep Reinforcement Learning Playground" margin="top" />}>
                </Column>
                <Column width="x">
                </Column>
                <Column width="x1">
                    <Media
                        media='video'
                        url='../media/work/education/rl-cnn.mov'
                        type='media'
                        gradient='false'
                        color={this.state.theme.primary} />
                </Column>
            </Container>
          <Container>
            <Column
              width='full'
              intro={<AnchorLink type="hyper" link="https://sites.google.com/view/exp-aes" text='Interpretable Aesthetic Analysis Model' margin="top" />}>
            </Column>
            <Column width="x">
            </Column>
            <Column width="x1">
                <Media
                  media='image'
                  url='../media/work/education/aes.png'
                  type='media'
                  gradient='false'
                  color={this.state.theme.primary} />
            </Column>
          </Container>
          <Column>
            <AnchorLink link="/publications" text="View all work" margin="top" />
          </Column>

        </Container>
      </div>
    );
  }
}

export default Home;



