import React, { Component, PropTypes } from 'react'

import { color } from '../../../vars'

import HeroFeature from '../../../components/HeroFeature'
import Container from '../../../components/Container'
import Column from '../../../components/Column'
import ClientLogo from '../../../components/ClientLogo'
import Block from '../../../components/Block'
import List from '../../../components/List'
import BlockGrid from '../../../components/BlockGrid'
import Media from '../../../components/Media'
import Process from '../../../components/Process'

import IntroContent from './intro.md'
import DesignContent from './design.md'
import SystemContent from './system.md'
import BrandContent from './brand.md'
import LinkIntro from "./intro-link.md";
import PICIntro from "../Graphic/intro-pic.md";
import YTIntro from "../Graphic/intro-yitong.md";
import ButterflyIntro from "../Graphic/intro-butterfly.md";

class Brand extends Component {
  componentWillMount() {
    this.state = {
      theme: {
        primary: color.white,
        secondary: color.blk
      }
    }
    document.body.style.backgroundColor = this.state.theme.primary
    document.body.style.color = this.state.theme.secondary
  }

  render() {
    const involvement = [
      { name: 'Workflows + product mapping' },
      { name: 'Wireframing' },
      { name: 'User testing' },
      { name: 'Lead design' },
      { name: 'Branding' },
      { name: 'Lead frontend development' },
      { name: 'Atomic design system' },
      { name: 'Responsive webapp design' }
    ]
    const processDiscover = [
      { name: 'Client workshops' },
      { name: 'Current product map' },
      { name: 'User interviews' }
    ]
    const processAnalyse = [
      { name: 'Team workshop' },
      { name: 'Product map' },
      { name: 'Concepts' }
    ]
    const processDevelop = [
      { name: 'Wireframes' },
      { name: 'Atomic design' },
      { name: 'Prototypes + MVP' },
      { name: 'Continuous improvement' }
    ]
    const processOutcome = [
      { name: 'Product with trained up internal team' },
      { name: 'Savings of over £1m' },
      { name: 'Handover + training' }
    ]
    return (
      <div>
        <HeroFeature headline='Brand Design' image='brand' color={this.state.theme.primary} />
        <Container>
          <Column width="third">
            <ClientLogo client="pic" />
          </Column>
          <Column
              width="twoThird"
              intro="ECCV2018 Person in Context Workshop and Challenge"
              content={PICIntro}
          >
          </Column>
        </Container>
        <hr />
        <Container>
          <Column width="third">
            <ClientLogo client="yitong" />
          </Column>
          <Column
              width="twoThird"
              intro="Alibaba Medical Insight"
              content={YTIntro}
          >
          </Column>
        </Container>
        <hr />
        <Container>
          <Column width="third">
            <ClientLogo client="butterfly" />
          </Column>
          <Column
              width="twoThird"
              intro="Alibaba Intelligent Design"
              content={ButterflyIntro}
          >
          </Column>
        </Container>
        <hr />
        <Container>
          <Column width="third">
            <ClientLogo client="link" />
          </Column>
          <Column
              width="twoThird"
              intro="Logo for Chain Tech"
              content={LinkIntro}
          >
          </Column>
        </Container>
      </div>
    )
  }
}

export default Brand



