import React from "react";
import { render } from "react-dom";
import {
  Router,
  Route,
  IndexRoute,
  browserHistory,
  applyRouterMiddleware,
} from "react-router";
import { useScroll } from "react-router-scroll";

import App from "./App";
import Home from "./pages/Home";
import Publications from "./pages/Publications";
import About from "./pages/About";
import CV from "./pages/CV";
import Contact from "./pages/Contact";
import Thanks from "./pages/Thanks";
import Work from "./pages/Work";

import Brand from "./pages/projects/Brand";
import UX from "./pages/projects/UX";
import QM from "./pages/projects/UX/quanmin";
import PG from "./pages/projects/UX/poster";
import Shinho from "./pages/projects/UX/shinho";
import Graphic from "./pages/projects/Graphic";
import Painting from "./pages/projects/Painting";
import UI from "./pages/projects/UI";
import Photography from "./pages/projects/Photography";

render(
  <Router history={browserHistory} render={applyRouterMiddleware(useScroll())}>
    <Route path={"/"} component={App}>
      <IndexRoute component={Home} />
      <Route path="/about" component={About} />
      <Route path="/publications" component={Publications} />
      <Route path="/about/cv" component={CV} />
      <Route path="/contact" component={Contact} />
      <Route path="/work" component={Work} />
      <Route path="/work/brand" component={Brand} />
      <Route path="/work/ux" component={UX} />
      <Route path="/work/ux/qm" component={QM} />
      <Route path="/work/ux/pg" component={PG} />
      <Route path="/work/ux/shinho" component={Shinho} />
      <Route path="/work/graphic" component={Graphic} />
      <Route path="/work/ui" component={UI} />
      <Route path="/work/painting" component={Painting} />
      <Route path="/work/photography" component={Photography} />
      <Route path="/thanks" component={Thanks} />
      <Route path="*" component={Home} />
    </Route>
  </Router>,
  document.getElementById("app")
);



