import React, { Component, PropTypes } from 'react'

import { color } from '../../../../vars'

import HeroFeature from '../../../../components/HeroFeature'
import Container from '../../../../components/Container'
import Column from '../../../../components/Column'
import ClientLogo from '../../../../components/ClientLogo'
import Block from '../../../../components/Block'
import List from '../../../../components/List'
import BlockGrid from '../../../../components/WideBlock'
import Media from '../../../../components/Media'
import Button from '../../../../components/Button'
import Process from '../../../../components/Process'

class PG extends Component {
    componentWillMount() {
        this.state = {
            theme: {
                primary: color.white,
                secondary: color.education
            }
        }
        document.body.style.backgroundColor = this.state.theme.primary
        document.body.style.color = this.state.theme.secondary
    }

    play() {
        const audio = document.getElementById('audio')
        audio.play()
    }

    render() {
        const involvement = [
            { name: 'Workflows + product mapping' },
            { name: 'Wireframing' },
            { name: 'Art + film direction' },
            { name: 'Lead design' },
            { name: 'Illustration' },
            { name: 'Lead frontend development' },
            { name: 'Responsive webapp design' }
        ]
        const processDiscover = [
            { name: 'Client workshops' },
            { name: 'Current market alternatives' }
        ]
        const processAnalyse = [
            { name: 'Team workshop' },
            { name: 'Product map' },
            { name: 'Concepts' }
        ]
        const processDevelop = [
            { name: 'Wireframes' },
            { name: 'Hi-fi designs' },
            { name: 'Prototypes + MVP' },
            { name: 'Develop product with continuous releases' }
        ]
        const processOutcome = [
            { name: 'Product released' },
            { name: 'Handover + training' }
        ]
        return (
            <div>
                <HeroFeature headline='Poster Generation App' image='ux' color={this.state.theme.primary} />
                <Container>
                    <Column width="full">
                        <Media
                            media="image"
                            position="marginBottomNegative"
                            url="../../images/work/poster.png"
                            alt="Full size of the pebble {code} website"
                        />
                    </Column>
                </Container>
            </div>
        )
    }
}

export default PG


