import React, { Component, PropTypes } from 'react'

import { color } from '../../../vars'

import HeroFeature from '../../../components/HeroFeature'
import Container from '../../../components/Container'
import Column from '../../../components/Column'
import Block from '../../../components/Block'
import List from '../../../components/List'
import BlockGrid from '../../../components/BlockGrid'
import Media from '../../../components/Media'
import Vimeo from '../../../components/Vimeo'
import Button from '../../../components/Button'

import IntroContent from './intro.md'
import LearnContent from './learn.md'
import ClientLogo from "../../../components/ClientLogo";

class UI extends Component {
  componentWillMount() {
    this.state = {
      theme: {
        primary: color.white,
        secondary: color.blk
      }
    }
    document.body.style.backgroundColor = this.state.theme.primary
    document.body.style.color = this.state.theme.secondary
  }

  render() {
    const involvement = [
      { name: 'Handcrafted letterforms on paper' },
      { name: 'Glyph design within Illustrator' },
      { name: 'Font creation with Glyphs' }
    ]
    return (
      <div>
        <HeroFeature headline='UI Design' image='ui' color={this.state.theme.primary}/>
        <Container>
          <Column
              width="full"
              intro="Website for Chain Tech"
          >
          </Column>
          <Column width="full">
            <Media
                media="image"
                url="../images/work/chain-web1.png"
                alt="Full size of the pebble {code} website"
            />
          </Column>
          <Column width="full">
            <Media
                media="image"
                position="marginBottomNegative"
                url="../images/work/chain-web2.png"
                alt="Full size of the pebble {code} website"
            />
          </Column>
        </Container>
      </div>
    )
  }
}

export default UI



